.home {
    display: flex;
    justify-content: center;
    margin: 0 auto;
    max-width: 1500px;
}

.home__row {
    display: flex;
    z-index: 1;
    margin: 0 auto;
}

.home__image {
    width: 100%;
    z-index: -1;
    margin-bottom: -250px;
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}

@media (max-width:1016px) {
    .home {
        min-width: 1016px;
    }
}