.checkout {
    display: flex;
    padding: 20px;
    background-color: white;
    height: max-content;
}

.checkout__ad {
    width: 100%;
    margin-bottom: 10px;
}

.checkout__title {
    margin-right: 10px;
    padding: 10px;
    border-bottom: 1px solid lightgray;
}

@media (max-width:1016px) {
    .checkout {
        display: flex;
        justify-content: space-between;

        min-width: 975px;
    }

    .checkout__ad {
        max-width: 90%;

    }
}